
.course {
    text-align: center;
    max-width: 390px;
    margin: 20px auto 0 auto;
}

.course-thumbnail {
    width: 150px;
    margin: 20px auto 30px auto;
    display: block;
    border-radius:4px;
}

.description-cell {
    text-align: left;
    padding: 10px 0;
}

.duration-cell {
    text-align: center;
}

.duration-cell mat-icon {
    display: inline-block;
    vertical-align: middle;
    font-size: 20px;
}

.spinner-container {
    height: 360px;
    width: 390px;
    position: fixed;
}

.lessons-table {
    min-height: 360px;
    margin-top: 10px;
}

.spinner-container mat-spinner {
    margin: 130px auto 0 auto;
}
